<template>
  <div class="wrap">
    <section class="arg-wrapper">
      <div
        v-for="item in items"
        class="arg-item"
        :style="'height: ' + item.num + 'px'"
        :key="item.id"
        :active="item.active"
      >
      </div>
    </section>
    <button @click="shuffle">シャッフル</button>
    <button @click="quickSort(0, numbers.length - 1)">クイックソート</button>
    <button @click="bubbleSort()">バブルソート</button>
    <button @click="selectSort()">セレクトソート</button>
    <button @click="shellSort()">シェルソート</button>
  </div>
</template>

<script>
export default {
  data () {
    return {
      items: [],
      numbers: []
    }
  },
  mounted () {
    for (var i = 0; i < 500; i++) {
      this.numbers[i] = i
      this.items.push({id: i, num: this.numbers[i], active: false})
    }
  },
  methods: {
    async shuffle () {
      for (var i = this.numbers.length - 1; i > 0; i--) {
        var r = Math.floor(Math.random() * (i + 1))
        var tmp = this.numbers[i]
        this.numbers[i] = this.numbers[r]
        this.numbers[r] = tmp
        this.items[i].num = this.numbers[i]
        await this.timeout(1)
      }
    },
    async shellSort () {
      for (var step = parseInt(this.numbers.length / 2); step > 0; step = parseInt(step / 2)) {
        // 間隔をあけて挿入ソートを行う
        // 挿入する値を１つずつ取り出す繰り返し
        for (var i = step; i < this.numbers.length; i++) {
          // 挿入する値を一時的に保存する
          var tmp = this.numbers[i]
          this.items[i].active = true
          // 取り出した位置から前に向かって比較するfor文
          for (var j = i; j >= step; j -= step) {
            if (this.numbers[j - step] > tmp) {
              // 挿入する値が小さい場合、その値をstep幅だけ後ろへずらす
              this.numbers[j] = this.numbers[j - step]
              this.items[j].num = this.numbers[j]
              this.items[j].active = true
              await this.timeout(1)
              this.items[j].active = false
            } else {
              // 挿入する値が小さくなければ、処理を止める
              break
            }
          }
          // ずらす処理が終わったところに「挿入する値」をいれる
          this.numbers[j] = tmp
          this.items[i].active = false
          this.items[j].num = this.numbers[j]
        }
      }
    },
    async selectSort () {
      // 「最小値を入れる位置」を先頭から順番に選択していくfor文
      for (var i = 0; i < this.numbers.length - 1; i++) {
        // 最小値を探すアルゴリズム
        // 先頭の値を暫定の最小値として初期設定する
        var min = this.numbers[i]
        // 先頭の位置を保存する
        var k = i
        // 隣の位置から最後まで、最小値との比較を繰り返す
        for (var j = i + 1; j < this.numbers.length; j++) {
          // 比較して最小値を更新するif文
          if (min > this.numbers[j]) {
            min = this.numbers[j]
            k = j
          }
          this.items[j].active = true
          await this.timeout(1)
          this.items[j].active = false
        }
        // 交換するアルゴリズム
        // 「先頭の値」と「最小値の値」を交換する
        var tmp = this.numbers[i]
        this.numbers[i] = this.numbers[k]
        this.numbers[k] = tmp
        this.items[i].num = this.numbers[i]
        this.items[k].num = this.numbers[k]
      }
    },
    async bubbleSort () {
      for (var i = 0; i < this.numbers.length; i++) {
        // 後ろから前に向かって小さい値を浮かび上がらせるfor文
        for (var j = this.numbers.length - 1; j > i; j--) {
          // 隣りあう２つの値を比べて、後ろが小さければ交換する
          if (this.numbers[j] < this.numbers[j - 1]) {
            var tmp = this.numbers[j]
            this.numbers[j] = this.numbers[j - 1]
            this.numbers[j - 1] = tmp
            this.items[i].num = this.numbers[i]
            this.items[j].num = this.numbers[j]
            this.items[i].active = true
            this.items[j].active = true
            await this.timeout(1)
            this.items[i].active = false
            this.items[j].active = false
          }
        }
      }
    },
    async quickSort (startID, endID) {
      // 範囲の間にある値をピポットに設定する
      var pivot = this.numbers[Math.floor((startID + endID) / 2)]
      console.log(pivot)
      this.items[pivot].active = true
      // 引数を左端、右端として変数にいれる
      var left = startID
      var right = endID
      // ピポットより小さい値を左側へ、大きい値を右側へ分割する
      while (true) {
        // leftの値がpivotより小さければleftを一つ右へ移動する
        while (this.numbers[left] < pivot) {
          left++
        }
        // rightの値がpivotより小さければrightを一つ左へ移動する
        while (pivot < this.numbers[right]) {
          right--
        }
        // leftとrightの値がぶつかったら、そこでグループ分けの処理を止める。
        if (right <= left) {
          break
        }

        // rightとrightの値がぶつかっていない場合、leftとrightを交換
        // 交換後にleftを後ろへ、rightを前へ一つ移動する
        var tmp = this.numbers[left]
        this.numbers[left] = this.numbers[right]
        this.numbers[right] = tmp
        this.items[left].num = this.numbers[left]
        this.items[right].num = this.numbers[right]
        this.items[left].active = true
        this.items[right].active = true
        await this.timeout(20)
        this.items[left].active = false
        this.items[right].active = false

        left++
        right--
      }

      // 左側に分割できるデータがある場合、quickSort関数を呼び出して再帰的に処理を繰り返す。
      this.items[pivot].active = false
      if (startID < left - 1) {
        await this.quickSort(startID, left - 1)
      }
      // 右側に分割できるデータがある場合、quickSort関数を呼び出して再帰的に処理を繰り返す。
      if (right + 1 < endID) {
        await this.quickSort(right + 1, endID)
      }
    },
    timeout (ms) {
      return new Promise(resolve => setTimeout(resolve, ms))
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.arg-wrapper{
  display: flex;
  align-items: flex-end;
}
.arg-item{
  width: 2px;
  border-top: 2px solid blue;
  border-bottom: 2px solid blue;
}
.arg-item[active]{
  background-color: red;
}
</style>



